.user {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: row nowrap;
      flex-flow: row nowrap;
  -ms-flex-align: center;
      align-items: center;
  padding: 0.625rem 1rem;
  cursor: pointer;
  color: #fff;
  background-image: -webkit-linear-gradient(191deg, var(--dusk), var(--dark));
  background-image: -o-linear-gradient(191deg, var(--dusk), var(--dark));
  background-image: linear-gradient(259deg, var(--dusk), var(--dark));
  -webkit-box-shadow: 0 1px 0 0 var(--white), 0 -1px 0 0 var(--white);
          box-shadow: 0 1px 0 0 var(--white), 0 -1px 0 0 var(--white);
}
  .user:hover {
    background-image: -webkit-linear-gradient(191deg, var(--dark), var(--dusk));
    background-image: -o-linear-gradient(191deg, var(--dark), var(--dusk));
    background-image: linear-gradient(259deg, var(--dark), var(--dusk));
  }
    .user:hover .icon-go {
      -webkit-animation: bounce 600ms both infinite;
              animation: bounce 600ms both infinite;
    }

.user-image-container {
  width: 52px;
}

.user-image {
  border-radius: 50%;
  border: solid 1px var(--white);
}

.user-info {
  padding: 0.625rem;
  overflow: hidden;
}
  .user-info > div {
    overflow: hidden;
    -o-text-overflow: ellipsis;
       text-overflow: ellipsis;
  }

.user-name {
  font-weight: bold;
  white-space: nowrap;
}

.user-account {
  margin-top: 0.3125rem;
  font-size: 0.75rem;
  white-space: nowrap;
}
  .user-account span + span {
    margin-left: 20px;
  }

.user-add {
  -ms-flex: 1 1;
      flex: 1 1;
  min-width: 70px;
  text-align: right;
}
  .user-add img + img {
    margin-left: 5px;
  }

@-webkit-keyframes bounce {
  from,
  to {
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
  }

  50% {
    -webkit-transform: translate3d(-2px, 0, 0);
            transform: translate3d(-2px, 0, 0);
  }
}

@keyframes bounce {
  from,
  to {
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
  }

  50% {
    -webkit-transform: translate3d(-2px, 0, 0);
            transform: translate3d(-2px, 0, 0);
  }
}



